

*{

  font-family:Arial, Helvetica, sans-serif ;
}

body{
  padding: 60px;
}

input{
  color:gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


.card{
  align-content: center;
  border:1px solid gray;
  overflow: hidden;
  margin: 0 10px 10px 0;
  border-radius: 6px;

}

.card p{
  padding: 5px;
  text-align: center;

}


.card img{
  max-height: 170px;
}